/** Full-height Nitro user's banner in Discord settings **/
[class^=accountProfileCard-] [class*=bannerPremium-][style*=url] {
  aspect-ratio: 340/120;
  height: auto !important;
}
[class^=accountProfileCard-] [class*=bannerPremium-][style*=url] + [class^=userInfo-] [class*=avatar-] {
  top: unset;
  bottom: 238px;
}

/** Disable Streaming badge on Guild icons **/
[aria-label=Servers] [class^=listItem-] [class^=iconBadge-],
[aria-label=Servers] [class^=listItem-] rect[id$=-upper_badge_masks],
[aria-label=Servers] [class^=listItem-] use[href$=-upper_badge_masks] {
  display: none !important;
}

